var fetchPonyfill = require('fetch-ponyfill');

var objectAssign = require('es6-object-assign').assign;

var qs = require('qs');

var url = require('url');

var _fetchPonyfill = fetchPonyfill(),
    fetch = _fetchPonyfill.fetch;

var defaultOptions = {
  headers: {
    accept: 'application/json'
  }
};
module.exports = jsonClient;

function jsonClient(baseUrl, options) {
  var resolvedBaseUrl = baseUrl.replace(/\/*$/, '/');
  var baseOptions = mergeOptions(defaultOptions, options);
  return function jsonClientRequest(method, path, params, body, options) {
    var query = params ? '?' + qs.stringify(params) : '';
    var resolved = url.resolve(resolvedBaseUrl, path + query);
    var reqOptions = mergeOptions(baseOptions, options);
    return makeRequest(method, resolved, body, reqOptions);
  };
}

function makeRequest(method, fullUrl, body, options) {
  var overrideOptions = {
    method: method
  };

  if (body != null) {
    overrideOptions.body = JSON.stringify(body);
    overrideOptions.headers = {
      'content-type': 'application/json'
    };
  }

  var fetchOptions = mergeOptions(options, overrideOptions);
  return fetch(fullUrl, fetchOptions).then(function (response) {
    return response.text().then(function (body) {
      return [response, body];
    });
  }).then(function (result) {
    var response = result[0];
    var body = result[1];
    var code = response.status;
    var codeFriendly = response.statusText || 'Unknown';
    var codeStr = codeFriendly.toLowerCase().replace(/\s+/g, '_'); // 2xx - success

    if (response.ok) {
      if (!body || !body.length) return null;

      try {
        return JSON.parse(body);
      } catch (e) {
        var _error = new Error('invalid json');

        _error.code = 'invalid_json';
        _error.statusCode = code;
        _error.meta = {
          httpStatus: code,
          method: method,
          url: fullUrl,
          data: body
        };
        throw _error;
      }
    } // any non-success codes
    // includes 4xx, 5xx and some 3xx codes


    var error = new Error('HTTP ' + code + ': ' + codeFriendly);
    error.code = codeStr;
    error.statusCode = code;
    error.meta = {
      httpStatus: code,
      method: method,
      url: fullUrl,
      data: body
    };

    try {
      var json = JSON.parse(body);
      if (typeof json !== 'object' || Array.isArray(json)) throw new Error();

      if (typeof json.code === 'string') {
        Object.keys(json).forEach(function (key) {
          error[key] = json[key];
        });
      } else {
        error.meta = json;
      }
    } catch (e) {
      /**/
    }

    throw error;
  });
}

function mergeOptions(baseOptions, newOptions) {
  var resolvedNewOptions = newOptions || {};
  var headers = objectAssign({}, baseOptions.headers, resolvedNewOptions.headers);
  return objectAssign({}, baseOptions, newOptions, {
    headers: headers
  });
}